___CSS_LOADER_EXPORT___.push([module.id, `.modal {
  opacity: 1;
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #333333;
  transform: translateX(100%);
  top: 0;
  transition: transform 0.5s;
}
.modal.show {
  transform: translateX(50%);
}
.modal-content {
  display: flex;
  flex-direction: column;
  background-color: #0000;
  height: 100vh;
  width: 50%;
  border-right: 1px solid #ffff;
}
.play-button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 600;
  background-color: #ffff;
  color: #000;
  min-height: 3rem;
  cursor: pointer;
  width: 100%;
}
.play-button:hover {
  background-color: #d3d3d3;
}
.play-icon {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.5rem;
}
.headline {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.icons {
  display: flex;
  gap: 1rem;
}
.icon {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  border: 0.05rem solid #fff;
  padding: 0.5rem;
  cursor: pointer;
  fill: #fff;
}
.icon:hover {
  fill: #d3d3d3;
  border: 0.05rem solid #d3d3d3;
}
h1 {
  margin: 0;
  font-weight: 400;
  font-size: 1.75rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.rating {
  border: 0.05rem solid #e0e0e0;
  padding: 0.5rem;
  border-radius: 0.5rem;
  width: fit-content;
}
.title {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.view-content {
  padding: 1rem;
}
.meta-data {
  display: flex;
  margin-top: 0.5rem;
  color: #e0e0e0;
  font-size: 0.75rem;
  gap: 1rem;
  align-items: center;
}
.description {
  margin-top: 1rem;
  font-size: 1rem;
  line-height: 1.5;
  color: #e0e0e0;
  border-radius: 0.5rem;
}
