  min-width: 4.5rem;
  height: 2.05rem;
  background: #3d3c3c;
  border-radius: 1rem;
  position: relative;
  cursor: pointer;
  z-index: -1;
}

.circle {
  width: 2rem;
  height: 2rem;
  background: white;
  border-radius: 50%;
  position: absolute;
  z-index: 1;
}

.slider {
  display: flex;
  align-items: center;
  position: absolute;
  height: 100%;
  width: 100%;
  transform: translateX(0);
  transition: transform 0.3s ease-in-out;
  z-index: 1;
}

.slider.on {
  transform: translateX(calc(100% - 2rem));
}

.icons {
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.icon {
  display: flex;
  fill: #000;
  width: 1.75rem;
}
