___CSS_LOADER_EXPORT___.push([module.id, `.img-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 0.5rem;
  background: linear-gradient(to left, #333 0%, #ccc 100%);
  background-size: 300%;
  animation: shimmer 1s infinite linear;
}
.img-wrapper.selected {
  border: 0.15rem solid #ffffff;
}

@keyframes shimmer {
  0% {
    background-position: -100% 0;
  }
  100% {
    background-position: 100% 0;
  }
}

.img {
  display: flex;
  aspect-ratio: 16/9;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 0.5rem;
}

.logo {
  position: absolute;
  bottom: 0.5rem;
  right: 0.5rem;
  width: 15%;
  height: auto;
  margin: 0.25rem 0.5rem;
}
